import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/bind',
      name: 'Bind',
      component: () => import('@/views/Bind')
    },
    {
      path: '/',
      name: 'Search',
      component: () => import('@/views/Search')
    }
  ]
})
